.contact{
    background-color: #364d79;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.contact > h2 {
    padding: 2%;
    margin-top: 0;
    color: white;
    text-align: center;
}

.icones {
    display: flex;
    height: 200px;
    width: 400px;
    margin-left: 30px;
    flex-direction: column;
    color: white;
}

.item {
    display: flex;
    justify-content: space-between;
}